import React from 'react'
import '../css/style.css';

function Header(){
    const firstName = "Faris"
    const lastName = "Kazmi"
    const date = new Date();
    return(
        <header className="navbar"> Hello {`${firstName} ${lastName}`}!, it is currently about {date.getHours() % 12} o'clock! </header>
    )

}

export default Header